<template>
  <div class="content">
    <div class="head" v-show="isFixed">
			<span @click="$router.push('/main/city')">
				<span class="cit">{{ $store.state.cityName }}</span><i class="iconfont icon-zhankai"></i></span>
      <div>电影</div>
      <div class="right_head"></div>
    </div>
    <header class="header">
      <swiper :key="navlist.length">
        <img class="swiper-slide img" v-for="value in navlist" :src="value.imgUrl" alt="">
      </swiper>
      <ul :class="['nav',isFixed?'navfixed':'']" ref="ul">
        <div class="nav_bottom" :style="range"></div>
        <router-link v-for="(item,index) in navdata" :to="item.path" custom v-slot="{navigate,isActive}" replace>
          <li @click="navigate" :class="isActive?isnow(index):''" :key="index">
            <div @click="isnow(index)">{{ item.name }}</div>
          </li>
        </router-link>
      </ul>
    </header>
    <footer :class="isFixed?'footermovie':''">
      <router-view></router-view>
    </footer>
  </div>
</template>

<script>
  import axios from "axios"

  export default {
    data() {
      return {
        navlist: [],
        navdata: [{
          id: 0,
          path: "/main/movie/on",
          name: "正在热映"
        },
          {
            id: 0,
            path: "/main/movie/off",
            name: "即将上映"
          }
        ],
        isFixed: false,
        isNow: 0
      }
    },
    components: {
      swiper: () => import("@/components/Myswiper.vue")
    },
    mounted() {
      window.addEventListener('scroll', this.hahaScroll)
      axios({
        url: `https://m.maizuo.com/gateway?type=2&cityId=${this.$store.state.cityId}&k=1209402`,
        headers: {
          'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"16339322892594864621420545","bc":"110100"}',
          'X-Host': 'mall.cfg.common-banner'
        }
      })
        .then(value => this.navlist = value.data.data)
    },
    methods: {
      hahaScroll() {
        let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body
          .scrollTop // 滚动条偏移量
        let offsetTop = document.querySelector('#container').offsetTop; // 要滚动到顶部吸附的元素的偏移量
        this.isFixed = scrollTop > offsetTop + 210 ? true : false; // 如果滚动到了预定位置，this.isFixed就为true，否则为false
      },
      isnow(index) {
        this.isNow = index;
        return "active"
      }
    },
    beforeDestroy() {
      window.removeEventListener('scroll', this.hahaScroll)
    },
    computed: {
      range() {
        return "left:" + this.isNow * 50 + "%;"
      },
    }

  }
</script>

<style scoped lang="scss">
  .content {
    width: 100%;
    background-color: #ededed;
    // width: 100%;
    // position: fixed;
    // top: 0;
    // overflow: auto;
    // height: calc(100% - 50px);

    .head {
      position: fixed;
      top: 0;
      width: 100%;
      height: 2.75rem;
      line-height: 2.75rem;
      margin: 0;
      padding: 0;
      z-index: 3000;
      background-color: #fff;
      display: flex;
      justify-content: space-between;
      font-size: 1.0625rem;
      border-bottom: 0.0625rem solid #ededed;

      span {
        max-width: 3.75rem;
        margin-left: 15px;
        font-size: 0.0625rem;
        display: flex;

        .cit {
          margin-left: 0;
          display: inline-block;
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
        }

        i {
          font-size: 0.5625rem;
        }
      }

      .right_head {
        width: 3.5rem;
        height: 2.75rem;
        margin-right: 15px;
      }
    }

    .img {
      width: 100%;
      height: 100%;
    }

    .header {
      position: relative;
      overflow: hidden;
    }

    .nav {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-around;
      height: 3.0625rem;
      border-bottom: 0.0625rem solid #ededed;
      background-color: #fff;
      position: relative;

      li {
        height: 3.0625rem;
        list-style: none;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 0.875rem;
      }

      .nav_bottom {
        position: absolute;
        width: 50%;
        height: 0.125rem;
        bottom: 0;
        left: 0;
        transition: all 200ms ease-in-out;
      }

      .nav_bottom::before {
        content: "";
        position: absolute;
        top: auto;
        left: 0;
        right: 0;
        bottom: 0;
        width: 3.5rem;
        background-color: #ff5f16;
        height: 100%;
        margin: 0 auto;
      }
    }

    .navfixed {
      position: fixed;
      top: 2.75rem;
    }

    .active {
      color: #ff6c17;
      position: relative
    }

    .footermovie {
      margin-top: 3.125rem;
    }
  }
</style>
